<template>
  <div class="resetPassword-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>ÈáçÁΩÆÂØÜÁ†Å</span>
      </div>
      <!-- Ë°®Âçï -->
      <el-form
        :model="pwdForm"
        :rules="pwdFormRules"
        ref="pwdFormRef"
        label-width="100px"
      >
        <el-form-item label="ÂéüÂØÜÁ†Å" prop="old_pwd">
          <el-input v-model="pwdForm.old_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="Êñ∞ÂØÜÁ†Å" prop="new_pwd">
          <el-input v-model="pwdForm.new_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="Á°ÆËÆ§Êñ∞ÂØÜÁ†Å" prop="re_pwd">
          <el-input v-model="pwdForm.re_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="updatePwdFn">‰øÆÊîπÂØÜÁ†Å</el-button>
          <el-button @click="resetFn">ÈáçÁΩÆ</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { updatePwdAPI } from '@/api'

export default {
  name: 'resetPassword',
  data() {
    // ÊåâÈÅìÁêÜÂ∫îËØ•Êúâ‰∏Ä‰∏™ÊóßÂØÜÁ†ÅÁöÑÈ™åËØÅÊé•Âè£ü§î
    // È™åËØÅÊóßÂØÜÁ†ÅÊòØÂê¶Ê≠£Á°Æ

    // Ê£ÄÊµãÊñ∞ÊóßÂØÜÁ†ÅÊòØÂê¶Áõ∏Âêå
    const samePwd = (rule, value, callback) => {
      if (value === this.pwdForm.old_pwd) {
        return callback(new Error('Êñ∞ÊóßÂØÜÁ†Å‰∏çËÉΩÁõ∏ÂêåÔºÅ'))
      }
      callback()
    }
    // Ê£ÄÊµã‰∏§Ê¨°Êñ∞ÂØÜÁ†ÅÊòØÂê¶‰∏ÄËá¥
    const rePwd = (rule, value, callback) => {
      if (value !== this.pwdForm.new_pwd) {
        return callback(new Error('‰∏§Ê¨°Êñ∞ÂØÜÁ†Å‰∏ç‰∏ÄËá¥ÔºÅ'))
      }
      callback()
    }

    return {
      // Ë°®ÂçïÁöÑÊï∞ÊçÆÂØπË±°
      pwdForm: {
        old_pwd: '',
        new_pwd: '',
        re_pwd: ''
      },
      // Ë°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      pwdFormRules: {
        old_pwd: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur' },
          {
            pattern: /^\S{6,15}$/,
            message: 'ÂØÜÁ†ÅÈïøÂ∫¶ÂøÖÈ°ªÊòØ6-15‰ΩçÁöÑÈùûÁ©∫Â≠óÁ¨¶‰∏≤',
            trigger: 'blur'
          }
        ],
        new_pwd: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å', trigger: 'blur' },
          {
            pattern: /^\S{6,15}$/,
            message: 'ÂØÜÁ†ÅÈïøÂ∫¶ÂøÖÈ°ªÊòØ6-15‰ΩçÁöÑÈùûÁ©∫Â≠óÁ¨¶‰∏≤',
            trigger: 'blur'
          },
          { validator: samePwd, trigger: 'blur' }
        ],
        re_pwd: [
          { required: true, message: 'ËØ∑ÂÜçÊ¨°Á°ÆËÆ§Êñ∞ÂØÜÁ†Å', trigger: 'blur' },
          {
            pattern: /^\S{6,15}$/,
            message: 'ÂØÜÁ†ÅÈïøÂ∫¶ÂøÖÈ°ªÊòØ6-15‰ΩçÁöÑÈùûÁ©∫Â≠óÁ¨¶‰∏≤',
            trigger: 'blur'
          },
          { validator: rePwd, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // ‰øÆÊîπÂØÜÁ†Å->ÁÇπÂáªÁ°ÆËÆ§
    updatePwdFn() {
      this.$refs.pwdFormRef.validate(async (valid) => {
        if (!valid) return false // Êú™ÈÄöËøáÊ†°È™åÊã¶‰Ωè

        const { data: res } = await updatePwdAPI(this.pwdForm)
        if (res.code !== 0) return this.$message.error('Êõ¥Êñ∞ÂØÜÁ†ÅÂ§±Ë¥•ÔºÅ')

        this.$message.success('Êõ¥Êñ∞ÂØÜÁ†ÅÊàêÂäüÔºÅ')
        this.$refs.pwdFormRef.resetFields()

        // ÈáçÁΩÆÂØÜÁ†ÅÂêéÁöÑÊìç‰Ωú
        this.$store.commit('updateToken', '')
        this.$router.push('/login')
      })
    },
    // ÈáçÁΩÆÊåâÈíÆ->ÁÇπÂáª‰∫ã‰ª∂
    resetFn() {
      this.$refs.pwdFormRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-form {
  width: 500px;
}
</style>
